<template>
  <div>
    <el-row class="df jcc" :gutter="20">
      <el-col :span="16">
        <el-row class="chatWindw df ffcn">
          <vue-scroll :ops="ops" ref="vs" @handle-scroll="scrollEvent">
            <div class="p20">
              <div v-for="(item,index) in post_data" :key="item.id" :id="['id'+item.id]">
                <div class="time df aic jcc" v-if="index&&item.time-post_data[index-1].time>120000">
                  <span class="df aic jcc">{{item.time|formatDate}}</span>
                </div>
                <div class="df aifs ptb10" :class="[item.user_id==userInfo.user_id?'jcfe':'jcfs']">
                  <el-avatar
                    :class="[item.user_id==userInfo.user_id?'o2':'o0']"
                    shape="square"
                    size="medium"
                    :src="item.user_head"
                  ></el-avatar>
                  <div class="df ffcn mlr10" style="max-width: 75%;">
                    <span style="color:#606266;font-size:12px;">{{item.user_name}}</span>
                    <div class="text o1" v-html="item.msg"></div>
                  </div>
                </div>
              </div>
            </div>
          </vue-scroll>
        </el-row>
        <el-row class="sendText">
          <el-col>
            <audio src="http://uaoie.top/cv/audio/3761.mp3" id="audio"></audio>
          </el-col>
          <el-col>
            <el-input
              type="textarea"
              maxlength="2048"
              autofocus
              class="inputSen"
              v-model="form.msg"
              placeholder="请输入..."
              show-word-limit
            ></el-input>
          </el-col>
          <el-col class="df jcfe">
            <el-button
              class="buttonSen"
              type="info"
              plain
              @click="setChatMsg"
              native-type="submit"
            >发送</el-button>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="4" class="onlineList">在线列表</el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isGetChatMsg: true,
      form: {
        msg: ""
      },
      userInfo: {},
      post_data: [],
      ops: {
        rail: {
          background: "#f5f5f5",
          border: "1px solid #cecece",
          size: "7px"
        },
        bar: {
          background: "#999",
          keepShow: true,
          size: "5px",
          minSize: 0
        },
        scrollButton: {
          enable: true,
          background: "#cecece"
        },
        scrollPanel: {
          easing: "easeInQuad",
          speed: 800
        },
        vuescroll: {
          wheelScrollDuration: 0,
          wheelDirectionReverse: false
        }
      }
    };
  },
  created() {
    this.userInfo = JSON.parse(localStorage.getItem("userInfo"));
    this.$socket.emit("getChatMsg");
    window.onkeyup = e => {
      if (e.code === "Enter" && !e.shiftKey) {
        this.setChatMsg();
      }
    };
  },
  mounted() {},
  methods: {
    scrollEvent() {
      const { v, h } = this.$refs["vs"].getScrollProcess();
      if (v === 0 && this.isGetChatMsg) {
        this.isGetChatMsg = false;
        this.$socket.emit("getChatMsg", this.post_data[0].id);
      }
    },
    async setChatMsg() {
      if (this.form.msg == "") return;
      if (this.form.msg == "\n") {
        this.form.msg = "";
        return;
      }
      await this.$socket.emit("setChatMsg", {
        msg: this.form.msg.replace(/\n/g, "<br/>").replace(/\s/g, "&nbsp;"),
        user_id: this.userInfo["user_id"]
      });
      this.form.msg = "";
    }
  },
  sockets: {
    connect(res) {
      this.$socket.emit("joinRoom", 11111);
    },
    //获取在线人
    joinRoom(res) {
      console.log("joinRoom", res);
    },
    //获取信息
    getChatMsg(res) {
      if (!res.length) return;
      this.isGetChatMsg = true;
      this.post_data.push(...res);
      this.post_data.sort((i, j) => {
        return i.id - j.id;
      });
      let id = res[res.length - 1].id;
      this.$nextTick(() => {
        if (id === this.post_data[this.post_data.length - 1].id) {
          if (
            this.post_data[this.post_data.length - 1].user_id !=
            this.userInfo.user_id
          ) {
            let audio = document.getElementById("audio");
            audio.play();
          }
          this.$refs["vs"].scrollIntoView(`#id${id}`, 500);
        } else {
          this.$refs["vs"].scrollIntoView(`#id${id}`, 0);
        }
      });
    }
  }
};
</script>

<style>
.time > span {
  background: #dadada;
  color: #fff;
  font-size: 11px;
  padding: 4px;
}
.onlineList {
  background: #f5f5f5;
  height: 450px;
}
.inputSen {
}
.buttonSen {
}
.sendText {
}
.text {
  word-break: break-all;
  background: #fff;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  color: #606266;
  border: 1px solid #ebeef5;
  padding: 10px;

  border-radius: 3px;
}
.chatWindw {
  background: #f5f5f5;
  height: 450px;
}
</style>


